import { fromJS } from 'immutable';
//import { constants } from 'zlib';
import * as constants from './constants';

const defaultState = fromJS({
    topicList: [],
    assessmentList: [],
    questionList: [],
    questionPage: 1,
    showScroll: false,
    inputValue: '',
    messageList: [{
        id: 1,
        type: "output",
        text: "Hello, how can I help you today?"
    },{
        id: 2,
        type: "input",
        text: "What is a CNN?"
    },{
        id: 3,
        type: "output",
        text: "In deep learning, a convolutional neural network is a class of deep neural networks, most commonly applied to analyzing visual imagery. CNNs use a variation of multilayer perceptrons designed to require minimal preprocessing."
    },{
        id: 4,
        type: "input",
        text: "What is a RNN?"
    },{
        id: 5,
        type: "output",
        text: "A recurrent neural network is a class of artificial neural network where connections between nodes form a directed graph along a temporal sequence. This allows it to exhibit temporal dynamic behavior. Unlike feedforward neural networks, RNNs can use their internal state to process sequences of inputs."
    }]
});

const updateHomeList = (state, action) => {
    return state.merge({
        'topicList': fromJS(action.topicList),
        'assessmentList': fromJS(action.assessmentList),
        'questionList': fromJS(action.questionList)
    });
}

const addHomeList = (state, action) => {
    return state.merge({
        'questionList': state.get('questionList').concat(action.list),
        'questionPage': action.nextPage
    });
}

export default (state = defaultState, action) => {
    switch(action.type){
        case constants.UPDATE_HOME_LIST:
            return updateHomeList(state, action)
        
        case constants.ADD_HOME_LIST: 
            return addHomeList(state, action)

        case constants.TOGGLE_SCROLL_TOP:
            return state.set('showScroll',action.show);
            
        case constants.CHANGE_INPUT_VALUE:
            const newState = JSON.parse(JSON.stringify(state));
            //state.set('inputValue', action.value);
            newState.inputValue = action.value;
            return newState

        case constants.ADD_MESSAGE:
            const newS = JSON.parse(JSON.stringify(state));
            newS.messagelist.push({id:7, type: "input", text: 'a'});
            newS.inputValue = '';
            return newS

        default:
            return state;
    }
}