import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { actionCreators }  from '../store';
import { 
    ChatboxWrapper, 
    ChatboxTitle, 
    ChatboxList, 
    ChatboxItem, 
    ChatboxBtm, 
    ChatboxInput, 
    Button 
} from '../style';

class Chatbox extends PureComponent {
    render() {
        const { inputValue, changeInputValue, handleClick, list } =this.props;
        return (
            <ChatboxWrapper>
                <ChatboxTitle>Chat Bot</ChatboxTitle>
                {
                    list.map((item) => {
                        return (                            
                                <ChatboxList key={item.get('id')}>
                                    <ChatboxItem className={item.get('type')}>
                                        {item.get('text')}
                                    </ChatboxItem>
                                </ChatboxList> 
                        );
                    })
                }
                <ChatboxBtm>
                    <ChatboxInput value={inputValue} onChange={changeInputValue}/>
                    <Button className='enter' onClick={handleClick}>Enter</Button>
                </ChatboxBtm>
            </ChatboxWrapper>            
        )
    }
}

const mapState = (state) => ({
    inputValue: state.getIn(['home', 'inputValue']),
    list: state.getIn(['home', 'messageList'])
})

const mapDispatch = (dispatch) => {
     return {
        changeInputValue(e) {
            dispatch(actionCreators.changeInputValue(e));
        },

        handleClick() {
            dispatch(actionCreators.handleClick());
        }
    }
}

export default connect(mapState, mapDispatch)(Chatbox);